.SkillsPage {
    padding-top: 5rem;
    /* border-style: solid; */
}

.skillsTitle {
    font-size: 2.5rem;
    color: orange;
    font-family: Viga;
    /* border-style: solid; */
    text-align: center;
}

.skillsContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.skillsContainer1 {
    padding-top: 1rem;
    justify-content: center;
    align-items: center;
    display: flex;
    gap: 0.5rem;
    flex-wrap: wrap;
    max-width: 1000px;;
    padding-bottom: 2rem;
}

.skillsLogoContainer {
    border-style: solid;
    border-color: #24354B;
    float: left;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    transition: 1s;
}

.skillsLogoContainer:hover{
    transform: scale(1.1);
    transition: 1s;
}

.skillCont {
    display: flex;
    justify-content: center;
    width: 100px;
}

.skillsLogo {
    height: 100px;
}

.skillsLogo2 {
    width: 100px;
    height: 100px;
}

.skillsNameExt {
    font-family: Righteous;
    color: #24354B;
    font-size: 1rem;
    transition: 1s;
}

.skillsName {
    font-family: Righteous;
    color: #24354B;
    font-size: 0rem;
    transition: 1s;
}

@media (max-width: 1000px) {
    .skillsContainer1 {
        max-width: 900px;
    }
}

@media (max-width: 950px) {
    .skillsContainer1 {
        max-width: 850px;
    }
}

@media (max-width: 880px) {
    .skillsContainer1 {
        max-width: 750px;
    }
}

@media (max-width: 770px) {
    .skillsContainer1 {
        max-width: 650px;
    }
}

@media (max-width: 660px) {
    .skillsContainer1 {
        max-width: 550px;
    }
}

@media (max-width: 550px) {
    .skillsContainer1 {
        max-width: 430px;
    }
}

@media (max-width: 450px) {
    .skillsContainer1 {
        max-width: 300px;
    }
}