* {
    box-sizing: border-box;
}

body {
    margin: 0;
}

.aLine {
    background-color: #C0E6E5;
    display: flex;
    height: 1px;
    width: 100%;
    border-radius: 10%;
}

.blinkersOrange {
    animation: blink 1s linear infinite;
  }

@keyframes blinkOrange {
    50% {
        background-color: orange;
    }
}

@keyframes blinkOrangeTxt {
    50% {
        color: orange;
    }
}