.site-title {
    float: left;
}

.site-title-container {
    display: flex;
    flex-direction: row;
    align-items: baseline;
}

.site-title-initial {
    font-size: 2rem;
    letter-spacing: 0.3rem;
    font-family: VarelaRound;
    color: ivory;
}

.site-title-back {
    color: #A2B4BF;
    font-size: 2rem;
    letter-spacing: 0.3rem;
    font-weight: bold;
    font-family: VarelaRound;
}

.nav-container{
    background-color: #24354B;
    z-index: 998;
    width: 100%;
    padding: 0 5rem;
    /* position: fixed; */
}

.container {
    margin: 1rem;
    display: flex;
    justify-content: center;
    /* min-height: 100%; */
    /* background-color: ivory; */
}

.nav {
    background-color: #24354B;
    color: ivory;
    display: flex;
    justify-content: space-between;    
    align-items: center;
    gap: 2rem;
    top: 0;
    position: fixed;
    width: 100%;
    z-index: 999;
}

.nav ul{
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 3rem;
    font-family: VarelaRound;
}

.nav li {
    border-radius: 0.5rem;
    padding: 0.25rem;
}

.navLinks {
    display: inline-flex;
    align-items: center;
    flex-direction: row;
    gap: 0.5rem;
    
}

.nav li.active {
    /* background-color: #C0E6E5; */
    /* color: ivory; */
    display: inline-block;
    text-decoration: underline;
    text-decoration-thickness: 0.15rem;
    /* text-decoration-color: #C0E6E5; */
    text-decoration-color: orange;
    text-underline-offset: 0.4rem;
    transition: 0.4s;
}

.nav li:hover {
    text-decoration: underline;
    text-decoration-thickness: 0.15rem;
    /* text-decoration-color: ivory; */
    color: orange;
    text-decoration-color: orange;
    text-underline-offset: 0.4rem;
    transform: scale(1.1);
    transform-origin: bottom right;
    transition: transform 0.4s ease-in;
}

.nav a {
    font-weight: bold;
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: block;
    align-items: center;
    padding: .25rem;
}

/* .nav i {
    color: #BBDBEA;
} */

.toggle-button {
    position: absolute;
    top: 0.75rem;
    right: 1rem;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 22px;
    cursor: pointer;
}

.bar {
    height: 3px;
    width: 100%;
    background-color: #D1DBE7;
    border-radius: 10px;
    animation: blinkOrange 1.5s linear infinite;
}

.expanded {
    padding-right: 5rem;
}

.footer {
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: #24354B;
    position: absolute;
    color: ivory;
    /* bottom: 0; */
    /* left: 0; */
}

.loadingImg {
    max-height: 10rem;
}

@media (max-width: 900px) {
    .nav-container{
        padding: 0 1rem;
    }

    .toggle-button{
        display: flex;
    }

    .nav .expanded {
        /* display:  none; */
        width: 100%;
    }

    .nav {
        flex-direction: column;
        align-items: flex-start;
        color: #24354B;
    }

    .nav ul{
        width: 100%;
        flex-direction: column;
        gap: 0.15rem;
    }

    .nav li {
        text-align: center;
        padding: 0rem
    }

    .nav.active {
        display: flex;
    }

    .nav.nav.active{
        display: flex;
    }

    .nav li.active {
        background-color: #F8F3E8F5;
        color: #FF7300;
        text-decoration: none;
        /* color: ivory; */
    }

    .nav li:hover {
        background-color: #F8F3E8;
        color: #24354B;
        text-decoration: none;
        transform-origin: center;
    }

    .expanded {
    position: absolute;
    background-color: #D1DBE7F5;
    z-index: 997;
    top: -400%;
    left: 0;
    width: 100%;
    /* height: 50vh; */
    overflow: hidden;
    padding: 3em;
    transition: all 2s cubic-bezier(0.16, 1, 0.1, 1);
    }
}