.AboutPage {
    padding-top: 3rem;
}

.interestContainer{
    padding-top: 5rem;
    display: flex;
    flex-direction: row;
    /* border-style: solid; */
    justify-content: center;
    align-items: center;
    padding-bottom: 3rem;
}

.interestMsg {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* border-style: solid; */
    float: left;
    color: #24354B;
}

.interestMsgTitle {
    display: flex;
    justify-content: center;
    font-weight: bold;
    color: orange;
    font-family: Viga;
    font-size: 3rem;
    /* border-style: solid; */
}

.interestMsgText {
    text-align:justify;
    max-width: 46rem;
    min-width: none;
    font-size: 1.2rem;
    padding-left: 6rem;
    font-family: itim;
}

.interestImg {
    display: inline-flex;
}

.intImg {
    height: 15rem;
    border-radius: 15rem;
    background-color: orange;
}

.intImg:hover {
    animation: bounce-7 2.5s infinite;
    animation-timing-function: cubic-bezier(0.280, 0.840, 0.420, 1);
}

.studiesInfo {
    display: inline-flex;
    justify-content: center;
    flex-direction: column;
    /* border-style: solid; */
}

.AbtInfoTitle {
    font-family: viga;
    font-size: 3rem;
    color: orange;
    text-align: center;
    /* border-style: solid; */
}

.AbtInfoContainer {
    display: flex;
    flex-direction: column;
    /* border-style: solid; */
    /* padding-bottom: 3rem; */
    border-radius: 3rem;
    border-color: rgba(0,0,0,.5);
    padding-top: 1rem;
    padding-bottom: 2rem;    
}

.AbtInfoContainer:hover {
    transform: scale(1.05);
    border-top: solid;
    box-shadow:  2em 4em 6em -2em rgba(0,0,0,.5),
    1em 2em 3.5em -2.5em rgba(0,0,0,.5);
}

.AbtInfoContainer2 {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-top: 2rem;
    /* border-style: solid; */
}

.AbtInfoText {
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 100%;
    padding-left: 3rem;
    /* border-style: solid; */
}

.AbtInfoContainer3 {
    display: flex;
    flex-direction: row;
    justify-content: center;
    font-family: Righteous;
    font-size: 2rem;
    color: #24354B;;
}

.AbtInfoText1 {
    padding-right: 0.7rem;
}

.AbtInfoContainer4 {
    display: flex;
    flex-direction: row;
    justify-content: center;
    font-family: Righteous;
    font-size: 1.5rem;
    color: #24354B;;
}

.AbtInfoText2 {
    padding-right: 0.7rem;
}

.AbtInfoLogoImg {
    width: 15rem;
    /* border-style: solid; */
}

.AbtInfoLogoImgNPCC {
    width: 15rem;
    border-radius: 15rem;
    /* border-style: solid; */
}

.ExtPadding {
    padding-top: 3rem;
    padding-bottom: 3rem;
}

@media (max-width: 1200px) {    
    .AbtInfoTitle {
        font-size: 2.5rem;
    }
    .AbtInfoContainer3 {
        flex-direction: column;
    }
}

@media (max-width: 1000px) {
    .AbtInfoTitle {
        font-size: 2rem;
    }
    
    .AbtInfoContainer2 {
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    
    
    .AbtInfoContainer3 {
        font-size: 1.5rem;
        flex-direction: column;
    }
    
    .AbtInfoText1 {
        padding-right: 0rem;
    }
    
    .AbtInfoContainer4 {
        font-size: 1.5rem;
        flex-direction: column;
    }
    
    .AbtInfoText2 {
        padding-right: 0rem;
    }

    .interestContainer {
        flex-direction: column;
    }

    .interestMsgText {
        padding-left: 0rem;
    }
}

@media (max-width: 650px) {
    .AbtInfoTitle {
        font-size: 1.5rem;
    }
    
    .AbtInfoContainer2 {
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    
    
    .AbtInfoContainer3 {
        font-size: 1.2rem;
        flex-direction: column;
    }
    
    .AbtInfoContainer4 {
        font-size: 1.2rem;
        flex-direction: column;
    }
    
    .AbtInfoLogoImg {
        width: 10rem;
    }

    .AbtInfoLogoImgNPCC {
        width: 10rem;
    }
}

@media (max-width: 600px) {
    .AbtInfoContainer2{
        flex-direction: column;
    }

    .AbtInfoText {
        padding-left: 0;
    }

    .AbtInfoContainer3 {
        padding-bottom: 1rem;
    }
}

@media (max-width: 400px) {
    .interestMsgText{
        font-size: 1rem;
        max-width: 20rem;
    }
}

@keyframes bounce-7 {
    0%   { transform: scale(1,1)      translateY(0); }
    10%  { transform: scale(1.1,.9)   translateY(0); }
    30%  { transform: scale(.9,1.1)   translateY(-100px); }
    50%  { transform: scale(1.05,.95) translateY(0); }
    57%  { transform: scale(1,1)      translateY(-7px); }
    64%  { transform: scale(1,1)      translateY(0); }
    100% { transform: scale(1,1)      translateY(0); }
}