.HomePage {
    float:left;
    display:inline-flex;
    flex-direction: column;
    justify-content: center;
    max-width: 70rem;
    /* border-style: solid; */
}

.profileImage {
    max-height: 20rem;
    border-radius: 15%;
    border-style: solid;
    transform: perspective(800px) rotateY(-20deg);
    box-shadow:  2em 4em 6em -2em rgba(0,0,0,.5),
    1em 2em 3.5em -2.5em rgba(0,0,0,.5);
    transition: 1.5s;
}

.profileImage:hover {
    transform: perspective(800px) rotateY(0deg) translateY(-1rem) rotateX(0deg);
    transition: 2s;
    filter: blur(0);
    opacity: 1;
}

.WelcomeMsgContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    float: left;
    /* border-style: solid;     */
    font-family: Righteous;
    padding: 0 3rem;
    padding-top: 8rem;
    list-style: none;
    flex-wrap: wrap;
    padding-bottom: 6rem;
}

.WelcomeMsgTextContainer {
    /* border-style: solid;     */
    padding-right: 15rem;
    color: #24354B;
}

.WelcomeMsg1Container {
    display: flex;
    /* border-style: solid;     */
    font-size: 2rem;
    gap: 1rem;
}

.WelcomeMsg2Container {
    display: flex;
    padding-top: 1rem;
    font-size: 1.7rem;
    align-items: end;
}

.WelcomeMsg2_1 {
    font-size: 1.7rem;
    font-family: Righteous;
    font-weight: bold;
    color: orange;
    padding-right: 0.7rem;
}

.WelcomeMsg3Container {
    display: flex;
    align-items: baseline;
    /* border-style: solid; */
    font-size: 3rem;
    min-width: 28rem;
    padding-top: 3rem;
}

.WelcomeMsg3_1 {
    padding-right: 0.8rem;
    /* border-style: solid; */
}

.RTE1 {
    /* border-style: solid; */
    color: orange
}

.waving {
    animation: wave 2.5s infinite;
    transform-origin: 70% 70%;
}

.scrolling-wrapper {
    /* border-style: solid;
    border-color: orange; */
    padding-top: 5rem;
    padding-bottom: 5rem;
    max-width: 60rem;
    /* min-width: 0; */
    margin: auto;
}

.carouselHL {
    display: flex;
    justify-content: center;
    font-size: 3rem;
    font-family: Viga;
    font-weight: bold;
    color: orange;
    padding-bottom: 2rem;
}

.contactContainer{
    padding-top: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    /* border-style: solid; */
    padding-bottom: 3rem;
}

.contactTitle {
    display: inline-flex;
    flex-direction: row;
    align-items: baseline;
    font-size: 2.5rem;
    font-family: Viga;
    color:#24354B;
}

.contactTitle1 {
    font-size: 3rem;
    color: orange;
    padding-right: 0.7rem;
}

.contactContainer2 {
    display: flex;
    flex-direction: row;
    /* border-style: solid; */
    justify-content: center;
    align-items: center;
    gap: 5rem;
    width: 100%;
    padding-top: 2.5rem;
}

.InfIcon {
    display: flex;
    justify-content: center;
    align-items: center;
    /* border-style: solid; */
    font-size: 2.5rem;
    color: ivory;
    border-color: #24354B;
    border-radius: 3rem;
    background-color: #24354B;
    width: 4.5rem;
}

.ExtInf {
    border: solid;
    border-color: #24354B;
    border-radius: 3rem;
    display: flex;
    flex-direction: row;
    color: #24354B;
    align-items: center;
    /* font-size: 2rem; */
    font-family: Righteous;
    /* float: left; */
}

.InfTxtExt{
    padding: 0 1.5rem;
    transition: 2s;
    font-size: 2rem;
}

.InfTxt {
    /* padding: 0 1.5rem; */
    transition: 2s;
    font-size: 0rem;
}

.photoStyle {
    border-radius: 3rem;
    height: 400px;
    padding: 0 1.5rem;
}

@keyframes wave {
    0% {
        transform: rotate(0deg);
    }
    10% {
        transform: rotate(14deg);
    }
    20% {
        transform: rotate(-8deg);
    }
    30% {
        transform: rotate(14deg);
    }
    40% {
        transform: rotate(-4deg);
    }
    50% {
        transform: rotate(10deg);
    }
    60% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(0deg);
    }
}

@media (max-width: 1000px) {
    .WelcomeMsgContainer {
        flex-direction: column;
        padding-right: 0;
        padding-left: 0;

    }

    .WelcomeMsgTextContainer {
        padding-right: 0rem;
        padding-bottom: 5rem;
    }

    .WelcomeMsg3Container {
        padding-top: 3rem;
        min-width: 20rem;
    }

    .WelcomeMsg1Container {
        justify-content: center;
        gap: 0;
    }

    .WelcomeMsg2Container {
        justify-content: center;
    }

    .WelcomeMsg3Container {
        justify-content: center;
    }

    .scrolling-wrapper {
        max-width: 50rem;
    }
}

@media (max-width: 900px) {
    .scrolling-wrapper {
        max-width: 40rem;
    }
}

@media (max-width: 700px) {
    .scrolling-wrapper {
        max-width: 30rem;
    }

    .contactContainer2 {
        flex-direction: column;
        gap: 2rem;
    }
    
    .InfTxt {
        transition: 2s;
        font-size: 2rem;
    }

    .homeA {
        width: 100%;
    }
}

@media (max-width: 550px) {
    .scrolling-wrapper {
        max-width: 20rem;
    }
}

@media (max-width: 400px) {
    .profileImage {
        max-height: 15rem;
    }

    .WelcomeMsg1Container {
        font-size: 1.5rem;
    }

    .WelcomeMsg2Container {
        font-size: 1.5rem;
    }

    .WelcomeMsg2_1 {
        font-size: 1.5rem;
    }

    .WelcomeMsg3Container {
        flex-direction:column;
        font-size: 2rem;
    }
    
    .WelcomeMsg3_1 {
        width: 100%;
        text-align: center;
    }

    .RTE1 {
        width: 100%;
        text-align: center;
    }
    
    /* .scrolling-wrapper {
        max-width: 15rem;
    } */
}